<!--
 * @Author: zgl
 * @Date: 2020-12-17 20:59:47
 * @LastEditTime: 2021-01-08 08:58:19
 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: \工作项目协同开发\operation_system\src\components\shopManagement\appeal\Appeal.vue
-->
<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title">
        <h2 style="text-align:center">POA申诉管理</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu class="el-menu-vertical-demo" unique-opened>
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div v-if="checkedSite.length > 0" class="info">
        <el-row>
          <el-col :span="24">
            <span>渠道：{{ checkedPlatform }}</span>
            <span>站点：{{ checkedSite }}</span>
            <span>国家：{{ checkedCountry }}</span>
            <span>时间范围：</span>
            <el-date-picker
              v-model="daterange"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
            <span>输入搜索</span>
            <el-input v-model="questionDetail"></el-input>

            <el-button type="primary" size="medium" @click="queryInfo"
              >查询</el-button
            >
          </el-col>
        </el-row>
      </div>
      <div v-else class="info">
        <el-row>
          <el-col :span="24">
            <span>渠道：全部</span>
            <span>站点：全部</span>
            <span>国家：全部</span>
            <span class="search">时间范围：</span>
            <el-date-picker
              v-model="daterange"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
            <span class="search search_last">输入搜索：</span>
            <el-input v-model="questionDetail"></el-input>

            <el-button type="primary" @click="queryInfo">查询</el-button>
          </el-col>
        </el-row>
      </div>
      <div class="upload">
        <el-button type="primary" size="mini" @click="uploadClick"
          >新增申诉记录</el-button
        >
        <el-button type="primary" size="mini" @click="editClick"
          >编辑申诉记录</el-button
        >
        <el-button type="primary" size="mini" @click="removeAppeal"
          >删除</el-button
        >
      </div>
      <div class="table">
        <el-table
          :data="appealFileList"
          border
          style="width: 100%"
          max-height="700"
          @selection-change="handleSelectionChange"
          ref="appealTableRef"
        >
          <el-table-column align="center" type="selection"></el-table-column>
          <el-table-column
            align="center"
            type="index"
            label="序号"
            width="60"
          ></el-table-column>
          <el-table-column
            prop="1"
            align="center"
            label="渠道"
            width="80"
          ></el-table-column>
          <el-table-column
            prop="2"
            label="站点"
            align="center"
            width="60"
          ></el-table-column>
          <el-table-column
            prop="3"
            label="国家"
            align="center"
            width="60"
          ></el-table-column>
          <el-table-column
            prop="4"
            label="case编号"
            width="80"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="5"
            align="center"
            label="问题类型"
          ></el-table-column>
          <!-- <el-table-column prop="7" label="POA申诉文件">
            <template slot-scope="scope">
              <a
                :href="_urlzgl+scope.row[7]"
                target="_blank"
              >{{scope.row[7].slice(scope.row[7].lastIndexOf('/')+1)}}</a>
            </template>
          </el-table-column>-->
          <el-table-column
            align="center"
            prop="8"
            label="上传人员"
            width="80"
          ></el-table-column>
          <el-table-column
            prop="9"
            align="center"
            label="上传时间"
          ></el-table-column>
          <el-table-column label="详情" align="center">
            <template slot-scope="scope">
              <el-button
                type="primary"
                size="mini"
                @click="lookAppeal(scope.row)"
                >查看</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          style="text-align:center"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>

    <!-- 新增申诉记录对话框 -->
    <el-dialog
      :visible.sync="uploadAppealDialogVisible"
      width="60%"
      center
      @close="uploadAppealDialogClose"
      class="upload-dialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title="新增POA申诉记录"
    >
      <!-- 表单区域 -->
      <el-form
        :model="uploadAppealForm"
        :rules="formRules"
        ref="uploadAppealRef"
        label-width="140px"
      >
        <el-form-item label="渠道" prop="platform">
          <el-select v-model="uploadAppealForm.platform" placeholder="请选择">
            <el-option
              v-for="(item, index) in platformList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家" prop="country">
          <el-select
            v-model="uploadAppealForm.country"
            placeholder="请选择"
            @change="selectChange"
          >
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点" prop="site">
          <el-select v-model="uploadAppealForm.site" placeholder="请选择">
            <el-option
              v-for="(item, index) in siteList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="问题类型" prop="type">
          <el-input v-model="uploadAppealForm.type"></el-input>
        </el-form-item>
        <el-form-item label="case编号" prop="caseCode">
          <el-input v-model="uploadAppealForm.caseCode"></el-input>
        </el-form-item>
        <el-form-item label="记录申诉详细信息" prop="detailInfo">
          <el-input
            v-model="uploadAppealForm.detailInfo"
            type="textarea"
            :rows="12"
          ></el-input>
        </el-form-item>
        <el-upload
          class="upload-demo"
          drag
          action="123"
          multiple
          :before-upload="beforeUpload"
          :http-request="uploadFile"
          :on-remove="handleRemove"
          ref="upload"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="uploadAppealDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="uploadAppealConfirm">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑申诉记录对话框 -->
    <el-dialog
      :visible.sync="editAppealDialogVisible"
      width="50%"
      center
      @close="editAppealDialogClose"
      class="upload-dialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title="编辑POA申诉详情"
    >
      <!-- 表单区域 -->
      <el-form
        :model="editAppealForm"
        :rules="formRules"
        ref="editAppealRef"
        label-width="170px"
      >
        <el-form-item label="渠道">
          <el-select
            v-model="editAppealForm.platform"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in platformList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select
            v-model="editAppealForm.country"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            v-model="editAppealForm.site"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in siteList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="问题类型">
          <el-input v-model="editAppealForm.type" disabled></el-input>
        </el-form-item>
        <el-form-item label="case编号">
          <el-input v-model="editAppealForm.caseCode" disabled></el-input>
        </el-form-item>
        <el-form-item label="旧记录申诉详细信息">
          <el-input
            v-model="editAppealForm.detailInfo"
            type="textarea"
            :rows="12"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="新增记录申诉详细信息" prop="detailInfo">
          <el-input
            v-model="editAppealForm.newDetailInfo"
            type="textarea"
            :rows="12"
          ></el-input>
        </el-form-item>
        <el-form-item label="文件列表" prop="detailInfo">
          <div v-for="(item, index) in editAppealForm.fileList" :key="index">
            {{ item.slice(item.lastIndexOf("/") + 1) }}
            <i
              class="el-icon-circle-close"
              @click="removeFile(item, index)"
            ></i>
          </div>
        </el-form-item>
        <el-upload
          class="upload-demo"
          drag
          action="123"
          multiple
          :before-upload="beforeUpload"
          :http-request="uploadFile"
          :on-remove="handleRemove"
          ref="upload"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
          <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
        </el-upload>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAppealDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editAppealConfirm">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 查看申诉详细信息 -->
    <el-dialog
      :visible.sync="lookAppealDialogVisible"
      width="60%"
      center
      class="upload-dialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      title="查看POA申诉详情"
    >
      <!-- 表单区域 -->
      <el-form
        :model="editAppealForm"
        :rules="formRules"
        ref="editAppealRef"
        label-width="140px"
      >
        <el-form-item label="渠道" prop="platform">
          <el-select
            v-model="editAppealForm.platform"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in platformList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家" prop="country">
          <el-select
            v-model="editAppealForm.country"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in countryList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点" prop="site">
          <el-select
            v-model="editAppealForm.site"
            placeholder="请选择"
            @change="selectChange"
            disabled
          >
            <el-option
              v-for="(item, index) in siteList"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="问题类型" prop="type">
          <el-input v-model="editAppealForm.type" disabled></el-input>
        </el-form-item>
        <el-form-item label="case编号" prop="caseCode">
          <el-input v-model="editAppealForm.caseCode" disabled></el-input>
        </el-form-item>
        <el-form-item label="记录申诉详细信息" prop="detailInfo">
          <el-input
            v-model="editAppealForm.detailInfo"
            type="textarea"
            :rows="12"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="文件列表" prop="detailInfo">
          <div v-for="(item, index) in editAppealForm.fileList" :key="index">
            {{ item.slice(item.lastIndexOf("/") + 1) }}
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="lookAppealDialogVisible = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      platformOnce: [],
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      // 品牌备案文件列表
      appealFileList: [],
      // 新增申诉记录对话框
      uploadAppealDialogVisible: false,
      uploadAppealForm: {
        platform: "",
        country: "",
        site: "",
        type: "",
        caseCode: "",
        detailInfo: ""
      },
      // 编辑申诉记录
      editAppealDialogVisible: false,
      editAppealForm: {
        platform: "",
        country: "",
        site: "",
        type: "",
        caseCode: "",
        detailInfo: "",
        newDetailInfo: "",
        id: "",
        // 获取已存在的文件列表
        fileList: [],
        // 获取删除的文件列表
        removeFileList: []
      },
      // 选中的文件数量
      fileData: [],
      // 上传的文件
      file: "",
      // 上传对话框 下拉框数据
      platformList: ["Amazon"],
      countryList: [],
      siteList: [],
      // storeList:[],

      // 验证表单
      formRules: {
        platform: [
          { required: true, message: "请选择渠道", trigger: "change" }
        ],
        site: [{ required: true, message: "请选择站点", trigger: "change" }],
        country: [{ required: true, message: "请选择国家", trigger: "change" }],
        type: [{ required: true, message: "请输入问题类型", trigger: "blur" }],
        caseCode: [
          { required: true, message: "请输入case编号", trigger: "blur" }
        ],
        detailInfo: [
          { required: true, message: "请输入申诉详细信息", trigger: "blur" }
        ]
      },
      // 多选框选中个数
      selectNum: 0,
      // 选中的行数据
      selection: [],
      // 查询时间
      checkedTime: "",
      // 查看申诉记录对话框
      lookAppealDialogVisible: false,
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50,

      // 时间范围搜索
      daterange: [],
      // 问题类型和申诉记录搜索
      questionDetail: "",
      // 进入查询状态
      isQuery: false
    };
  },
  computed: {},
  watch: {
    // 日期范围被清空 会变成null，这不是我们想要的，我们要将其变成空数组
    daterange(val1, val2) {
      if (val1 == null) {
        this.daterange = [];
      }
    }
  },
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;
        // 页面刷新 先默认展示京汇的 同时在导航那里添加default-active="0-0-1"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        // this.checkedPlatform = this.menuPlatFormList[0];
        // this.checkedCountry = this.showCountryList[0];
        // this.checkedSite = this.lastSiteList[0];
        this.thirdClick("", "", "", 1);
      });
    },
    // 获取上传中 的下拉框数据
    getSelectData() {
      this.$api.getSelectData().then(res => {
        console.log(res);
        this.platformList = res.data.platform_list;
        this.countryList = res.data.country_list;
        this.siteList = res.data.site_list;
        // 去重
        this.platformList = [...new Set(this.platformList)];
        this.countryList = [...new Set(this.countryList)];
        this.siteList = [...new Set(this.siteList)];
      });
    },
    // 获取国家列表
    getCountryList() {
      this.$api.getCountryList().then(res => {
        this.countryList = res.data.data;
      });
    },
    onceClick(index) {
      // if(this.platformOnce[index] == "") {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
      this.platformOnce[index] = 1;
      console.log(this.platformOnce);
      // }
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      let page = 1;
      this.$api.queryAppeal(platform, country, site, page).then(res => {
        console.log(res);
        this.appealFileList = res.data.data;
        this.total = res.data.all_number;
        this.$nextTick(() => {
          this.$refs["appealTableRef"].bodyWrapper.scrollTop = 0;
        });
        this.currentPage = 1;
      });
    },
    // 多选框改变事件
    handleSelectionChange(selection) {
      this.selectNum = selection.length;
      this.selection = selection;
      console.log(selection);
    },
    // 行点击就选中多选框
    appealRowClick(row) {
      this.$refs.appealTableRef.toggleRowSelection(row);
    },
    // 查看申诉详情
    lookAppeal(info) {
      this.editAppealForm.platform = info[1];
      this.editAppealForm.country = info[3];
      this.editAppealForm.site = info[2];
      this.editAppealForm.type = info[5];
      this.editAppealForm.caseCode = info[4];
      this.editAppealForm.detailInfo = info[6];
      this.editAppealForm.fileList = info[7].split("@");
      this.lookAppealDialogVisible = true;
    },
    // 上传申诉文件相关事件
    uploadClick() {
      this.getCountryList();
      //点击
      this.uploadAppealDialogVisible = true;
    },
    uploadAppealConfirm() {
      //确认上传
      this.$refs.uploadAppealRef.validate(state => {
        if (!state) return;
        // 没有选择文件 返回
        // if (this.file == "") {
        //   this.$message.closeAll();
        //   return this.$message.error("请选择一个文件");
        // }
        let formData = new FormData();
        formData.append("complain_platform", this.uploadAppealForm.platform);
        formData.append("complain_country", this.uploadAppealForm.country);
        formData.append("complain_site", this.uploadAppealForm.site);
        formData.append("complain_type", this.uploadAppealForm.type);
        formData.append("complain_case", this.uploadAppealForm.caseCode);
        formData.append("detail_info", this.uploadAppealForm.detailInfo);
        // formData.append("file", this.file);
        for (let item of this.fileData) {
          formData.append("file_list", item);
        }
        this.$api.uploadAppeal(formData).then(res => {
          console.log(res);
          this.$message.success("上传成功");
          this.thirdClick(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite
          );
          this.uploadAppealDialogVisible = false;
          this.file = "";
          this.fileData = [];
          this.siteList = [];
        });
      });
    },
    uploadAppealDialogClose() {
      this.$refs.uploadAppealRef.resetFields();
      // 清除文件列表
      this.$refs.upload.clearFiles();
      this.file = "";
      this.fileData = [];
      this.siteList = [];
    },
    // 下拉框联合
    selectChange(val) {
      // console.log(val);
      this.$api.getSiteList(val).then(res => {
        console.log(res);
        this.siteList = res.data.data;
      });
    },
    beforeUpload(file) {
      //文件上传前发生
      // console.log(file);
      this.fileData.push(file);
      this.file = file;
    },
    uploadFile(file) {
      // console.log(file.file);
      // this.fileData.push(file.file);
    },
    // 文件移除时触发
    handleRemove(file) {
      for (let i in this.fileData) {
        if (this.fileData[i] === file.raw) {
          this.fileData.splice(i, 1);
        }
      }
      this.file = "";

      // 单个文件
      // this.fileData = [];
      // this.file = "";
    },
    // 删除申诉记录
    removeAppeal() {
      if (this.selectNum == 0) {
        this.$message.closeAll();
        return this.$message.info("请选择申诉模板");
      }
      let id = [];
      for (let item of this.selection) {
        id.push(item[0]);
      }
      console.log(id);

      this.$confirm("此操作将永久删除这些记录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteAppeal(id).then(res => {
            console.log(res);
            this.thirdClick(
              this.checkedPlatform,
              this.checkedCountry,
              this.checkedSite
            );
            this.$message.success("删除成功");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    // 编辑申诉记录
    editClick() {
      if (this.selectNum == 0) {
        this.$message.closeAll();
        return this.$message.info("请选择申诉模板");
      } else if (this.selectNum > 1) {
        this.$message.closeAll();
        return this.$message.info("一次只允许编辑一个申诉模板");
      }
      this.editAppealForm.platform = this.selection[0][1];
      this.editAppealForm.country = this.selection[0][3];
      this.editAppealForm.site = this.selection[0][2];
      this.editAppealForm.type = this.selection[0][5];
      this.editAppealForm.caseCode = this.selection[0][4];
      this.editAppealForm.id = this.selection[0][0];
      this.editAppealForm.detailInfo = this.selection[0][6];

      this.editAppealForm.fileList = this.selection[0][7].split("@");
      this.editAppealDialogVisible = true;
    },
    removeFile(item, index) {
      console.log(item, index);
      this.editAppealForm.removeFileList.push(item);
      this.editAppealForm.fileList.splice(index, 1);
    },
    editAppealDialogClose() {
      this.$refs.editAppealRef.resetFields();
      // 清除文件列表
      this.$refs.upload.clearFiles();
      this.file = "";
      this.fileData = [];
      for (let i in this.editAppealForm) {
        this.editAppealForm[i] = "";
      }
      this.editAppealForm.removeFileList = [];
      this.editAppealForm.fileList = [];
    },
    editAppealConfirm() {
      //确认上传
      this.$refs.editAppealRef.validate(state => {
        if (!state) return;
        // 没有选择文件 返回
        // if (this.file == "" && this.editAppealForm.newDetailInfo == "") {
        //   this.$message.closeAll();
        //   return this.$message.error("请选择文件或者编辑申诉信息");
        // }
        if (this.editAppealForm.newDetailInfo == "") {
          this.$message.closeAll();
          return this.$message.error("请编辑申诉信息");
        }
        // this.editAppealForm.fileList  ||  this.fileData
        if (
          this.editAppealForm.fileList.length == 0 &&
          this.fileData.length === 0
        ) {
          this.$message.closeAll();
          return this.$message.error("至少保留一份上传文件哟!");
        }

        let formData = new FormData();
        formData.append("complain_id", this.editAppealForm.id);
        // formData.append("insert_detail_info", this.editAppealForm.detailInfo);
        formData.append("complain_platform", this.editAppealForm.platform);
        formData.append("complain_country", this.editAppealForm.country);
        formData.append("complain_site", this.editAppealForm.site);
        formData.append("detail_info", this.editAppealForm.detailInfo);
        formData.append(
          "insert_detail_info",
          this.editAppealForm.newDetailInfo
        );
        // 暂时只有一个文件
        // formData.append("file_list", this.file);
        for (let item of this.fileData) {
          console.log(item);

          formData.append("file_list", item);
        }
        this.$api.editAppeal(formData).then(res => {
          console.log("编辑信息" + res);
          this.$api
            .removeFile(
              this.editAppealForm.id,
              this.editAppealForm.removeFileList
            )
            .then(res => {
              console.log("删除文件列表" + res);

              this.$message.success("编辑成功");
              this.thirdClick(
                this.checkedPlatform,
                this.checkedCountry,
                this.checkedSite
              );
              this.editAppealDialogVisible = false;
              this.file = "";
              this.fileData = [];
              for (let i in this.editAppealForm) {
                this.editAppealForm[i] = "";
              }
            });
        });
      });
    },
    // 分页
    handleCurrentChange(val) {
      console.log(val);
      // 非查询状态时，普通分页
      if (!this.isQuery) {
        this.$api
          .queryAppeal(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite,
            val
          )
          .then(res => {
            // console.log(res);
            this.appealFileList = res.data.data;
            this.total = res.data.all_number;
            this.$nextTick(() => {
              this.$refs["appealTableRef"].bodyWrapper.scrollTop = 0;
            });
          });
      }
      // 处于查询状态时
      else {
        this.$api
          .queryAppeal(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite,
            val,
            this.daterange[0],
            this.daterange[1],
            this.questionDetail
          )
          .then(res => {
            // console.log(res);
            this.appealFileList = res.data.data;
            this.total = res.data.all_number;
          });
      }
    },
    /**
     * @description: 问题类型和申诉记录搜索还有时间范围
     * @param {*}
     * @return {*}
     */
    queryInfo() {
      let page = 1;
      this.$api
        .queryAppeal(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.daterange[0],
          this.daterange[1],
          this.questionDetail
        )
        .then(res => {
          console.log(res);
          this.appealFileList = res.data.data;
          this.total = res.data.all_number;
          this.currentPage = 1;
          this.$nextTick(() => {
            this.$refs["appealTableRef"].bodyWrapper.scrollTop = 0;
          });
        });
      this.isQuery = true;
    }
    // queryInfo() {
    //   let page = 1;
    //   // 每次查询都要获取完整的列表，防止下一次的查询是在上一次查询的结果中继续的
    //   this.$api
    //     .queryAppeal(
    //       this.checkedPlatform,
    //       this.checkedCountry,
    //       this.checkedSite,
    //       page
    //     )
    //     .then(res => {
    //       console.log(res);
    //       this.appealFileList = res.data.data;
    //       this.total = res.data.all_number;
    //       this.currentPage = 1;
    //       console.log(this.daterange, this.questionDetail);
    //       // 创建一个变量保存被时间筛选后的列表
    //       let queryArr = [];
    //       // 如果时间有被编辑
    //       if (this.daterange.length !== 0) {
    //         for (let item of this.appealFileList) {
    //           let formatDate = new Date(item[9]);
    //           let formatDate1 = new Date(this.daterange[0]);
    //           let formatDate2 = new Date(this.daterange[1]);
    //           // 时间符合要求时
    //           if (
    //             (formatDate > formatDate1 || formatDate == formatDate1) &&
    //             (formatDate < formatDate2 || formatDate == formatDate2)
    //           ) {
    //             console.log("符合要求");

    //             queryArr.push(item);
    //             console.log(queryArr);
    //             // 如果时间选了，但是搜索没有编辑
    //             if (this.questionDetail == "") {
    //               this.appealFileList = queryArr;
    //             }
    //             // 时间和搜索都有编辑
    //             else {
    //               for (let i in queryArr) {
    //                 if (queryArr[i][7].indexOf(this.questionDetail) === -1) {
    //                   queryArr.splice(i, 1);
    //                 }
    //               }
    //               this.appealFileList = queryArr;
    //             }
    //           }
    //         }
    //         let bool = this.appealFileList.every(item => {
    //           let formatDate = new Date(item[9]);
    //           let formatDate1 = new Date(this.daterange[0]);
    //           let formatDate2 = new Date(this.daterange[1]);
    //           return (
    //             ((formatDate > formatDate1 || formatDate == formatDate1) &&
    //               (formatDate < formatDate2 || formatDate == formatDate2)) ===
    //             false
    //           );
    //         });
    //         if (bool === true) {
    //           this.appealFileList = [];
    //         }
    //       }
    //       // 时间没被编辑，判断搜索框
    //       else {
    //         // 如果搜索框有值
    //         if (this.questionDetail !== "") {
    //           for (let item of this.appealFileList) {
    //             if (item[7].indexOf(this.questionDetail) !== -1) {
    //               queryArr.push(item);
    //             }
    //           }
    //           this.appealFileList = queryArr;
    //         }
    //       }
    //     });
    // }
  },
  created() {
    // this.getSelectData();
    this.getMenuList();
  },
  mounted() {}
};
</script>
<style>
#ipi-board .el-textarea textarea {
  resize: none;
}
</style>
<style scoped>
#ipi-board .info span {
  margin-right: 20px;
}
#ipi-board .search {
  margin-right: 10px;
}
#ipi-board .search_last {
  margin-left: 20px;
}
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
  /* margin-top: 2vh; */
}
#ipi-board >>> input[type="file"] {
  display: none;
}
.title {
  margin-top: 2vh;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
  overflow: auto;
}
.info span {
  margin-right: 12px;
}
.info .el-input {
  display: inline-block;
  width: auto;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.upload-dialog .el-select,
.upload-dialog .el-input {
  width: 14vw;
}
.upload-dialog >>> .el-upload,
.upload-dialog >>> .el-upload-dragger {
  width: 100%;
}
.table {
  margin-top: 6px;
}
.el-icon-circle-close {
  cursor: pointer;
  margin-left: 16px;
}
#ipi-board >>> .el-range-separator {
  width: 7%;
}
</style>



